<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Tambah Mata Pelajaran</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-refresher slot="fixed" @ionRefresh="doRefresh($event)">
        <ion-refresher-content />
      </ion-refresher>
      <ion-content class="ion-padding">
        <ion-item>
          <ion-label position="floating">Nama Mata Pelajaran</ion-label>
          <ion-input
            v-model="subjectName"
            placeholder="Masukkan nama mata pelajaran"
          ></ion-input>
        </ion-item>
        <ion-button expand="block" @click="addSubject" style="color: white"
          >Tambah Mata Pelajaran</ion-button
        >

        <ion-grid class="ion-padding">
          <ion-row>
            <ion-col>
              <h2>Daftar Mata Pelajaran</h2>
              <table>
                <thead>
                  <tr>
                    <th>Nama Mata Pelajaran</th>
                    <th>Aksi</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="subject in subjectList" :key="subject.id">
                    <td>{{ subject.name }}</td>
                    <td>
                      <ion-button
                        color="warning"
                        @click="openEditModal(subject)"
                        >Edit</ion-button
                      >
                      <ion-button
                        color="danger"
                        @click="deleteSubject(subject.id)"
                        >Hapus</ion-button
                      >
                    </td>
                  </tr>
                </tbody>
              </table>
            </ion-col>
          </ion-row>
        </ion-grid>
      </ion-content>

      <ion-modal :is-open="isEditModalOpen" @did-dismiss="closeEditModal">
        <ion-header>
          <ion-toolbar>
            <ion-title>Edit Mata Pelajaran</ion-title>
            <ion-buttons slot="end">
              <ion-button @click="closeEditModal">Close</ion-button>
            </ion-buttons>
          </ion-toolbar>
        </ion-header>
        <ion-content>
          <ion-item>
            <ion-label position="floating">Nama Mata Pelajaran</ion-label>
            <ion-input v-model="editSubjectName"></ion-input>
          </ion-item>
          <ion-button expand="block" @click="updateSubject"
            >Simpan Perubahan</ion-button
          >
        </ion-content>
      </ion-modal>

      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonContent,
  IonRefresher,
  IonRefresherContent,
  IonButton,
  IonGrid,
  IonRow,
  IonCol,
  IonModal,
  IonItem,
  IonLabel,
  IonInput,
  IonToast,
} from "@ionic/vue";
import { ref, onMounted } from "vue";
import {
  getFirestore,
  collection,
  addDoc,
  getDocs,
  updateDoc,
  deleteDoc,
  doc,
  serverTimestamp,
} from "firebase/firestore";
import { firebaseApp } from "../firebase.js";

const db = getFirestore(firebaseApp);
const subjectName = ref("");
const subjectList = ref([]);
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});
const isEditModalOpen = ref(false);
const editSubjectName = ref("");
const editSubjectId = ref("");

const fetchSubjects = async () => {
  try {
    const querySnapshot = await getDocs(collection(db, "mataPelajaran"));
    subjectList.value = querySnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));
  } catch (error) {
    console.error("Error fetching subjects:", error);
    showToast(
      "Terjadi kesalahan saat mengambil data mata pelajaran.",
      "danger"
    );
  }
};

const addSubject = async () => {
  if (!subjectName.value) {
    showToast("Nama mata pelajaran tidak boleh kosong", "danger");
    return;
  }
  try {
    await addDoc(collection(db, "mataPelajaran"), {
      name: subjectName.value,
      createdAt: serverTimestamp(),
    });
    showToast("Mata pelajaran berhasil ditambahkan", "success");
    subjectName.value = ""; // Clear the input
    fetchSubjects(); // Refresh the list
  } catch (error) {
    console.error("Error adding subject:", error);
    showToast("Terjadi kesalahan saat menambahkan mata pelajaran", "danger");
  }
};

const openEditModal = (subject) => {
  editSubjectName.value = subject.name;
  editSubjectId.value = subject.id;
  isEditModalOpen.value = true;
};

const closeEditModal = () => {
  isEditModalOpen.value = false;
  editSubjectName.value = "";
  editSubjectId.value = "";
};

const updateSubject = async () => {
  if (!editSubjectName.value) {
    showToast("Nama mata pelajaran tidak boleh kosong", "danger");
    return;
  }
  try {
    await updateDoc(doc(db, "mataPelajaran", editSubjectId.value), {
      name: editSubjectName.value,
    });
    showToast("Mata pelajaran berhasil diperbarui", "success");
    closeEditModal();
    fetchSubjects(); // Refresh the list
  } catch (error) {
    console.error("Error updating subject:", error);
    showToast("Terjadi kesalahan saat memperbarui mata pelajaran", "danger");
  }
};

const deleteSubject = async (subjectId) => {
  try {
    await deleteDoc(doc(db, "mataPelajaran", subjectId));
    showToast("Mata pelajaran berhasil dihapus", "success");
    fetchSubjects(); // Refresh the list
  } catch (error) {
    console.error("Error deleting subject:", error);
    showToast("Terjadi kesalahan saat menghapus mata pelajaran", "danger");
  }
};

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message,
    duration: 2000,
    color,
  };
};

const doRefresh = (event) => {
  setTimeout(() => {
    fetchSubjects();
    event.target.complete();
  }, 2000);
};

onMounted(() => {
  fetchSubjects();
});
</script>

<style scoped>
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th,
td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
  font-weight: bold;
}

tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

tbody tr:hover {
  background-color: #f1f1f1;
}
</style>
